*{
  font-size: 100%;
}

.App {
  display: flex;
  text-align: center;
  flex-direction: column;
  
}
.fileupload{
  margin: 5px 5px 50px 5px;
}

.fileupload-title{
  font-size: 10vw;
}
.qnas{
  display: flex;
  flex-direction: column;
}
.qna{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.question{
  display: flex;
  justify-content: flex-start;
  font-size: 1.2em;
  font-weight:bold;
}
.options{
  display: flex;
  flex-direction: column;
  
}

.option-label{
  display: flex;
  justify-content: flex-start;
  margin:5px 5px 5px 5px;
  /* background: rgba(255, 0, 0, 0.466); */
  /* background-color: rgba(127, 255, 255, 0.473) */

}

input[type="radio"] {
  width: 10%; /* Điều chỉnh chiều rộng */
  height: 10%; /* Điều chỉnh chiều cao */
  margin-right: 5px; /* Khoảng trống giữa nút radio và nhãn */
  transform:scale(2);
}


